/**
 * Created by saiyingding  on 2018/5/29.
 * 本地存储 设置、获取、清除
 */
class Store {
    constructor(){
        this.store = window.localStorage;
    }
    set(key,value,expired,fn,){
        try {
            value = JSON.stringify(value);
        }catch (e){
            value = value;
        }
        if (expired){
          this.store[`${key}__expires__`] = Date.now() + 1000*60*expired
      }
        this.store.setItem(key,value);
        fn && fn();
    }
    get(key,fn){
        if(!key){
            throw new Error('没有找到key。');
            return;
        }
        if(typeof key === 'object'){
            throw  new Error('key不能是一个对象');
            return;
        }
        let value = this.store.getItem(key);
        const expired = this.store[`${key}__expires__`]||Date.now+1;
        const now = Date.now();
        if ( now >= expired ) {
            this.remove(key);
            return;
        }
        if(value !== null){
            try {
                value = JSON.parse(value);
            }catch (e){
                value = value;
            }
        }
        return value;
    }
    remove(key){
        this.store.removeItem(key);
        this.store.removeItem(`${key}__expires__`);
    }
}
export default new Store();


